@import "color-palette";

/* GLOBAL SETTINGS
   ============================= */

$theme-1: $lime-1;
$theme-2: $lime-2;

$border-radius-base: 3px;
$border-radius-large: 0;
$border-radius-small: 0;

$asset-path: "/soda/cms/";
$font-family-sans-serif:  "Noto Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
$fa-font-path: $asset-path + "fonts/font-awesome" !default;
$mdi-font-path: $asset-path + 'fonts/mdi' !default;
$image-path: $asset-path + "components/bootstrap-fileinput/img"; // bootstrap-fileinput plugin

/* CONTENT
   ============================= */

$body-bg: $slate-100;
$content-background: $slate-100;
$content-inner-background: $white;
$content-inner-border: $slate-090;
$content-inner-text: rgb(51, 51, 51);

/* LOGIN
   ============================= */

$login-box: $slate-010;
$login-box-text: $slate-060;
$login-box-link: $slate-060;
$login-box-link-hover: $slate-080;

/* NAVBAR
   ============================= */

$navbar-height: 60px;
$navbar-default-bg: $white;

$navbar-inverse-color: $slate-060;
$navbar-inverse-bg: $slate-010;
$navbar-inverse-border: transparent;

$navbar-inverse-link-color: $slate-060;
$navbar-inverse-link-hover-color: $white;
$navbar-inverse-link-hover-bg: transparent;
$navbar-inverse-link-active-color: $navbar-inverse-link-hover-color;
$navbar-inverse-link-active-bg: rgba(0, 0, 0, 0.3);

/* NAV TABS
   ============================= */

$nav-tabs-border-color:                     transparent;
$nav-tabs-link-hover-border-color:          transparent;

$nav-tabs-active-link-hover-bg:             $white;
$nav-tabs-active-link-hover-border-color:   $content-inner-border;

/* SIDEBAR
   ============================= */

$sidebar-background: $slate-040;
$sidebar-link: transparent;
$sidebar-link-text: $slate-060;
$sidebar-link-hover: $slate-050;
$sidebar-link-hover-text: $slate-080;
$sidebar-link-active: $slate-030;
$sidebar-link-active-text: $white;
$sidebar-link-active-sub: $slate-030;
$sidebar-link-active-sub-text: $white;
$sidebar-group-link: $slate-030;
$sidebar-group-link-text: $slate-060;
$sidebar-group-link-hover: $slate-040;
$sidebar-group-link-hover-text: $slate-080;
$sidebar-group-link-active: $slate-030;
$sidebar-group-link-active-text: $white;

/* OTHER
   ============================= */

$list-group-border: $content-inner-border;
$standard-shadow: 0 1px 3px rgba(0, 0, 0, 0.25);
$btn-default-bg: $slate-090;
$btn-default-color: $slate-020;

$input-border: $slate-090;
$input-border-radius: 0;

@import "../../../node_modules/bootstrap-sass/assets/stylesheets/bootstrap/variables";
